export default {
    search: ['affiliate.name', 'customer.tpc'],
    expand: ['affiliate'],
    headers: vm => [
        {
            text: 'Affiliate',
            align: 'start',
            value: 'affiliate.name',
        },


        {
            text: 'Deposits',
            value: 'count',
            class: ['font-weight-bold'],
            type: 'new|old',
           
        },
        {
            text: 'FTD\'s',
            value: 'count',
            class: ['font-weight-bold'],
            type: 'ftd',

        },
        { text: 'Total', value: 'amount', format: 'money' },
    ].filter(field => {
        if (!field.type) return true
        return field.type.split('|').includes(vm.affiliate_type)
    }),
    subheaders: vm => [

        {
            text: 'TP',
            align: 'start',
            value: 'customer.tpc',
            width: '10%'
        },
        {
            text: 'Account name',
            value: 'customer.name',
        },
        { text: 'Country', sortable: false, value: 'customer.country', format: 'country', width: '150' },
        {
            text: 'FTD',
            value: 'ftd',
            type: 'new|old',
            component: {
                is: () => import('./../../components/items/fields/FTD.vue'),
                bind: (_, item) => ({
                   get show () {
                       return item.type == 'rtn'
                   }
                })
            }
        },

        { text: 'Type', value: 'type', format: 'uppercase', class:'font-weight-bold' },

     
        { text: 'Amount', value: 'amount', format: 'money' },
        { text: 'Date', value: 'created_on', format: 'date' },
       
    ].filter(field => {
        if (!field.type) return true
         
        return field.type.split('|').includes(vm.affiliate_type)
    }),
}