<template>
  <v-container>
    <v-layout d-flex column>
      <v-flex>
        <date-picker v-model="dates" ></date-picker>
      </v-flex>
      <v-progress-linear indeterminate v-if="loading"></v-progress-linear>
 
    <v-tabs v-model="tab" class="my-6" show-arrows>
      <v-tab v-for="(tab, ind ) in tabs" :key="ind" >
        <v-icon class="mx-1">{{tab.icon}}</v-icon>
        <span >{{tab.title}}</span>
      </v-tab>
    </v-tabs>
     <v-layout d-flex  column >
      <v-flex class="my-6 d-flex">
        
        <v-row>
          <v-col>
             <v-text-field class="my-4" outlined v-model="search"
              prepend-inner-icon="mdi-magnify" hide-details 
              :placeholder="placeholder"
              dense style="max-width: 100%"
              clearable></v-text-field>
          </v-col>
          <v-col class="d-flex flex-column align-end"   >
            <span class="caption font-weight-bold " v-if="!tab">Group By</span>
            <v-btn-toggle  v-model="toggle_by" class="transparent" v-if="!tab">
              <v-btn class="px-2" :disabled="!toggle_by">
                <v-icon class="mx-1">mdi-account-box</v-icon>
                Customer
              </v-btn>
              <v-btn class="px-2" :disabled="toggle_by == 1">
                <v-icon class="mx-1">mdi-account-network-outline</v-icon>
                Affiliate
              </v-btn>
              <v-btn class="px-2" :disabled="toggle_by == 2">
                <v-icon class="mx-1">mdi-account</v-icon>
                Owner
              </v-btn>
            </v-btn-toggle>

            <v-select v-else-if="tab > 1" 
            outlined v-model="type"
              prepend-inner-icon="mdi-format-list-bulleted-type" hide-details 
              :placeholder="placeholder"
              dense style="max-width: 100%"
               class="my-4" 
            
            :items="[{ text: 'All', value: null }, { text: 'Sales', value: 'ftd' }, { text: 'Retentions', value: 'rtn' }]"></v-select>

          </v-col>
          
        </v-row>
      </v-flex>
      <v-flex cols="12" class="my-4"  v-if="!tab && !toggle_by"  >
   

        <v-alert dense color="info" outlined class="text-center">
          <div class=" font-weight-bold">
            ( {{upsales.count}}  ) Upsales
          </div>
         <v-row class="ma-0" >
          
           <v-col cols="12" sm="4">
              <small>Upsales FTD's:</small> <div class="success--text">{{ upsales.sumOfFtd | moneyFormat }}</div>
           </v-col>
           <v-col cols="12" sm="4">
             <small>Upsales RTN's:</small> <div class="success--text">{{ upsales.sumOfRtn | moneyFormat }}</div>
           </v-col>
           <v-col cols="12" sm="4">
             <small>Upsales Total:</small> <div class="success--text ">{{ upsales.sumOfTotal | moneyFormat }}</div>
           </v-col>
         </v-row>
        
          
        
        </v-alert>
      </v-flex>
      <v-flex cols="12" class="my-4"  v-if="tab == 1"  >
   

        <v-alert dense color="info" outlined >
          <div class=" font-weight-bold">
            ( {{upretention.count}}  ) Upsales 
          </div>
         <v-row class="ma-0" >
          
           <v-col cols="12" sm="4">
              <small>Total:</small> <div class="success--text">{{ upretention.sum | moneyFormat }}</div>
           </v-col>
          
         </v-row>
        
          
        
        </v-alert>
      </v-flex>

      <v-flex cols="12" class="my-4"  v-if="tab == 3"  >
        <v-tabs >
           <v-tab @click="fetchByType('ftd')"> 
            <v-icon class="mx-1">
              mdi-cash
            </v-icon>
            NEW FTD's
          </v-tab>
          <v-tab @click="fetchByType('new')"> 
            <v-icon class="mx-1">
              mdi-cash-plus
            </v-icon>
            Deposits from New FTD's
          </v-tab>
           <v-tab @click="fetchByType('old')"> 
            <v-icon class="mx-1">
              mdi-cash-plus
            </v-icon>
             Deposits from OLD FTD's
          </v-tab>
        </v-tabs>
      </v-flex>

    </v-layout>
    <v-divider ></v-divider>


    
     
      <v-flex cols="12"  >
       <data-table 
        :items="items" 
        :headers="headers"
        :expanded.sync="expanded"
        item-key="_id"
        single-expand 
        :show-expand="showExpanded"
        > 
        <template v-slot:expanded-item="{item, headers}">
          <tr class="grey lighten-2">
            <td class="pb-6 pt-2" :colspan="headers.length">
              <v-divider></v-divider>
              <data-table   :items="item.subItems || table.subItems" :headers="child_headers" :loading="table.loading" :parent="vm"></data-table>
              <v-divider></v-divider>
            
            </td>
          </tr>

          
        </template>
       </data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import utility from './../../utility'
import DatePicker from './../../components/dashboard/DatePicker' 
import DataTable from './../../components/DataTable'
import debounce from 'debounce'
import report_config from './config'
export default {
  components: {
    DatePicker,
    DataTable
  },
  computed: {
    vm () { return this },
    config () {
      return report_config[this.tab]
    },
    placeholder () {
      if (this.toggle_by == 0) return 'Search Customer / TP...';
      if (this.toggle_by == 1) return 'Search Customer / TP...';
      if (this.toggle_by == 2) return 'Search Affiliate...';
      if (this.toggle_by == 3) return 'Search Owner...';
      if (this.toggle_by == 4) return 'Search Payment Method...';
      if (this.toggle_by == 5) return 'Search Country...';
      return 'Search...'
    },
    active () {
      return this.tabs[this.tab]
    },
    searchFields () {

      return !this.tab ? this.config.search[this.groupBy] : this.config.search

    },
    groupBy () {
      return this.by[this.toggle_by]
    },
    headers () {
      return  this.tab < 2 ? this.config.headers[this.groupBy] :( typeof this.config.headers == 'function' ? this.config.headers(this) : this.config.headers)
    },
    items () {
      return this.loading ? [] : this.table.items
    },
    showExpanded () {
     
      return this.config.expand ? this.config.expand.indexOf(this.groupBy) > -1 : false
    },
    child_headers () {
      if (this.config.subheaders)
        return typeof this.config.subheaders == 'function' ?  this.config.subheaders(this) :  this.config.subheaders
      return this.groupBy == 'user' ? this.config.headers.user_customer : this.config.headers.customer

    }
  },
  data: () => ({
    affiliate_type: 'ftd',
    upsales: {
   
      count: 0,
      sumOfFtd: 0,
      sumOfRtn: 0,
      sumOfTotal: 0
    },
     upretention: {
      count: 0,
      sum: 0
    },
    type: null,
    expanded: [],
    dates:  utility.currentDateRange,
    toggle_by: 0,
    by: ['customer', 'customer', 'affiliate', 'user', 'payment_method', 'country'],
    tab: 0,
    tabs: [
      {
        title: "Upsale Sales",
        icon: "mdi-cash",
        api: {
          items: 'deposit/upsales_sales',
          total: 'deposit/upsales_sales',
        }
      },
       {
        title: "Upsale Retention",
        icon: "mdi-cash-plus",
        api: {
          items: 'deposit/upsales_retention',
          total: 'deposit/upsales_retention',
        }
      },
      {
        title: "Customer",
        icon: "mdi-account-box",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      },
      {
        title: "Affiliate",
        icon: "mdi-account-network-outline",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      },
      {
        title: "User",
        icon: "mdi-account",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      },
      {
        title: "Payment",
        icon: "mdi-credit-card",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      },
      {
        title: "Countries",
        icon: "mdi-flag",
        api: {
          items: 'deposit/report',
          total: 'deposit/report',
        }

      }
    ],
    // total:{
    //   items: [],
    //   headers: [
      
    //     {
    //       text: 'Deposits',
    //       value: 'count',
    //       class:['font-weight-bold', 'subtitle-1'],
    //       width: '120',
          
    //     },
    //     {
    //       text: 'Total',
    //       class:['font-weight-bold', 'subtitle-1'],
    //       value: 'amount',
    //       format: 'money'
    //     }
        
    //     ]

    // },
    table: {
      loading: false,
      subItems: [],
      items: [],
      headers: []
    },
    subLoading: false,
    loading: true,
    search: '',
   

  }),
  watch: {
    async expanded (ex) {
      if (!ex.length) return;
      const [ item ] = ex
      this.table.subItems = []
      switch (this.groupBy) {
        case 'affiliate':
          if (!this.tab) // upsales
            this.getAffiliateFtd(item)
          else 
            this.table.subItems = item.deposits || []

          break;
        case 'user':
            this.getUserFtd(item)
            break;
        default:
          break;
      }
    

   
    },
    search: debounce( function () {
      this.expanded = []
      this.fetch()
    }, 500),
    dates:{
      immediate:true,
      handler:'fetch' 
    },
    type () {
      this.search = ''
      this.fetch()
    },
    tab (v) {
      this.expanded = []
      this.affiliate_type = 'ftd'
      this.type = null
      this.toggle_by = !v ? 0 : v - 1
      this.search = ''
      this.fetch()
    },
    toggle_by () {
      this.expanded = []
      this.search = ''
      if (!this.tab)
        this.fetch()
    }
  },
  methods:{
    async getUserFtd (item) {
 

 
      const query =  { 
        created_on: this.dates.join('|'),
        group: 'user_customer',
        [this.groupBy]:  item._id
      } 
      
      
      this.table.loading = true
      const response = await this.get({ collection: this.active.api.items, 
        query
      })
      this.table.subItems = response.data
      this.table.loading = false

      
    },
    async getAffiliateFtd (item) {
        
      const grouped_by = item[this.groupBy]

 
      const query =  { 
        created_on: this.dates.join('|'),
        group: 'customer',
        [this.groupBy]:  grouped_by._id
      } 
      
      
      this.table.loading = true
      const response = await this.get({ collection: this.active.api.items, 
        query
      })

      this.table.subItems = response.data
      this.table.loading = false
    },
    async fetch () {
      this.loading = true
      if (!this.tab) {
        this.fetchUpsales()
      } else if (this.tab == 1) {
        this.fetchUpRtn()
      } else
      {
        this.fetchByType()
      }
      this.loading = false
        
       
    },
    async fetchByType (affiliate_type = 'ftd') {
       const query =  { 
        created_on: this.dates.join('|'),
        group: this.groupBy 
      } 

      if (this.groupBy == 'affiliate') {
        query.affiliate_type = affiliate_type
        this.affiliate_type = affiliate_type
      }


      if (this.type) {
        query.type = this.type
      }
      if (this.search) {
        query['orFields'] = this.searchFields
        query['orValues'] = this.searchFields.map(() => '~' + this.search.trim())
      }

 
      const response = await this.get({ collection: this.active.api.items, 
        query
      })
 

      this.table.items = response.data  

    },

    async fetchUpRtn () {
            const query =  { 
        created_on: this.dates.join('|'),
        group: this.groupBy 
      } 
      if (this.search) {
        query['orFields'] = this.searchFields
        query['orValues'] = this.searchFields.map(() => '~' + this.search.trim())
      }
 
      const response = await this.get({ collection: this.active.api.items, 
          query
        })
      console.log(response.data );
        
      this.table.items = response.data  
      const items = response.data
      // const upsalesales = response.data.filter(item => item.deposits )
     
      
      
      const isNum =  num => typeof num == 'number'
      const sum = items.length ?  items.reduce((prev, current) => (isNum(prev) ? prev : prev.amount) + current.amount) : 0
      const count = items.length ? items.length : 0
  

      this.upretention = {
        count,
        sum
      }
    },
    async fetchUpsales () {
       const query =  { 
        created_on: this.dates.join('|'),
        group: this.groupBy 
      } 
      if (this.search) {
        query['orFields'] = this.searchFields
        query['orValues'] = this.searchFields.map(() => '~' + this.search.trim())
      }
 
      const response = await this.get({ collection: this.active.api.items, 
          query
        })
      this.table.items = response.data  
      // console.log(response.data );
      const upsalesales = response.data.filter(item => item.deposits )
     
      
      
      const isNum =  num => typeof num == 'number'
      const sumOfFtd = upsalesales.length ?  upsalesales.reduce((prev, current) => (isNum(prev) ? prev : prev.amount) + current.amount) : 0
      const sumOfRtn = upsalesales.length ?  upsalesales.reduce((prev, current) => (isNum(prev) ? prev : prev.deposits.amount) + current.deposits.amount) : 0
      const sumOfTotal = upsalesales.length ?  upsalesales.reduce((prev, current) => (isNum(prev) ? prev : prev.total) + current.total) : 0
      const count = upsalesales.length ? upsalesales.length : 0


      this.upsales = {
        count,
        sumOfFtd,
        sumOfRtn,
        sumOfTotal
      }
     
      
      
    }
  } 
}


</script>

